
@import url("./assets/iconfont/iconfont.css");
* {
  margin: 0;
  padding: 0;
}
#app {
  font-family: PingFangSC-Light,'Avenir',Helvetica,Hiragino Sans GB,Arial,Microsoft YaHei,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  font-size: 14px;
}
body {
  background-color: #d2e7ea;
}
a,
a:focus,
a:hover {
  cursor: pointer;
  color: inherit;
  text-decoration: none;
}
a:focus,
a:active {
  outline: none;
}
input, textarea {
  outline: none;
}
input, input::-webkit-input-placeholder, textarea, textarea::-webkit-input-placeholder {
  font-family: PingFangSC-Light,'Avenir',Helvetica,Hiragino Sans GB,Arial,Microsoft YaHei,sans-serif;
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #1f2d3d;
}
::-moz-selection {
  background: #4cb3ff;
  color: #fff;
}
::selection {
  background: #4cb3ff;
  color: #fff;
}

/* form btn */
.form-save {
  width: 150px;
  margin: 30px auto;
  font-size: 18px;
  line-height: 2;
  background-color: #4ca2ae;
  color: #fff;
  text-align: center;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
}
.github-corner svg {
  fill: #4ca2ae;
  color: #fff;
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
}
.github-corner:hover .octo-arm {
  -webkit-animation: octocat-wave 560ms ease-in-out;
          animation: octocat-wave 560ms ease-in-out;
}
@-webkit-keyframes octocat-wave {
0%, 100% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
20%, 60% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg);
}
40%, 80% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
}
}
@keyframes octocat-wave {
0%, 100% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
20%, 60% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg);
}
40%, 80% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
}
}
@media (max-width: 500px) {
.github-corner .github-corner:hover .octo-arm {
    -webkit-animation: none;
            animation: none;
}
.github-corner .github-corner .octo-arm {
    -webkit-animation: octocat-wave 560ms ease-in-out;
            animation: octocat-wave 560ms ease-in-out;
}
}



